import React from 'react'
import './styles/Projects.css'
import { TradContext } from '../../contexts/TradContext';
import { Typography } from '@material-ui/core';
import githubIcon from "../../assets/githubIcon.svg"
const iconsSize = "90px"
const Projects = () => {
    let lang = React.useContext(TradContext);
    const projectsNames = ["ponctual", "dirCleaner", "winCrypt", "connect4", "nezzarChat", "slh"]
    const projectsData = [
        {
            name: "Ponctual",
            descriptionENG: "Application intended for schools, allowing to manage students, teachers, delays, absences etc ...",
            descriptionFR: "Application destinée aux écoles, permettant de gérer les élèves, les enseignants, les retards, les absences etc ...",
            github: "https://github.com/strak-stark/Ponctual",
            typeENG: ["courseProject", "course project"],
            typeFR: ["courseProject", "projet de cours"]
        },
        {
            name: "nezzarChat",
            descriptionENG: "Online chat using Kwick API",
            descriptionFR: "Chat en ligne utilisant l'API Kwick",
            github: "https://github.com/strak-stark/nezzarKwick",
            typeENG: ["courseProject", "course project"],
            typeFR: ["courseProject", "projet de cours"]
        },
        {
            name: "Connect4",
            descriptionENG: "Simple Connect4 made with HTML, CSS and JavaScript",
            descriptionFR: "Simple Puissance 4 fait avec HTML, CSS et JavaScript",
            github: "https://github.com/strak-stark/Connect4",
            typeENG: ["sideProject", "side project"],
            typeFR: ["sideProject", "projet personnel"]
        },
        {
            name: "Strak's Lsb Hider",
            descriptionENG: "Strak's Lsb Hider is a CLI oriented Linux project that help you embed files (images, videos, texts etc...) into Images using least significant bit of each pixel.",
            descriptionFR: `Strak's Lsb Hider est un projet Linux orienté "CLI" qui vous aide à intégrer des fichiers (images, vidéos, textes etc ...) dans des images en utilisant le bit de poid faible (LSB) de chaque pixel.`,
            github: "https://github.com/strak-stark/StrakLsbHider",
            typeENG: ["sideProject", "side project"],
            typeFR: ["sideProject", "projet personnel"]
        },
        {
            name: "WinCrypt",
            descriptionENG: 'WinCrypt is a CLI oriented Linux project that help you encrypt files (images, videos, texts etc...) into ".wc" files that can be secure and encrypted with a custom key.',
            descriptionFR: 'WinCrypt est un projet Linux orienté CLI qui vous aide à crypter des fichiers (images, vidéos, textes etc ...) en fichiers ".wc" qui peuvent être sécurisés et cryptés avec une clé personnalisée.',
            github: "https://github.com/strak-stark/WinCrypt",
            typeENG: ["sideProject", "side project"],
            typeFR: ["sideProject", "projet personnel"]
        },
        {
            name: "DirCleaner",
            descriptionENG: "DirCleaner is a python program that help you clear your PC desktop using tkinter UI.",
            descriptionFR: "DirCleaner est un programme python qui vous aide à vider le bureau de votre PC à l'aide de l'interface utilisateur de tkinter.",
            github: "https://github.com/strak-stark/DirCleaner",
            typeENG: ["sideProject", "side project"],
            typeFR: ["sideProject", "projet personnel"]
        },
    ]
    return (
        <section className="projects">

            {lang === "ENG"
                ? <h2>My last projects</h2>
                : <h2>Mes derniers projets</h2>
            }

            <div className="projectsCtnr">
                {projectsData.map(project => {
                    return (
                        <article className={`project ${project.name}`} key={project.name}>
                            
                            {lang === "ENG"
                                        ? <span className={`badge ${project.typeENG[0]}`}>{project.typeENG[1]}</span>
                                        : <span className={`badge ${project.typeFR[0]}`}>{project.typeFR[1]}</span>
                                    }
                            <div className="projectDataCtnr">
                                <div className="projectData">
                                    <h4>{project.name}</h4>
                                    {lang === "ENG"
                                        ?
                                        <Typography paragraph className="aboutDescription">
                                            {project.descriptionENG}
                                        </Typography>
                                        :
                                        <Typography paragraph className="aboutDescription">
                                            {project.descriptionFR}
                                        </Typography>
                                    }

                                    <a href={project.github} target="_blank" rel="noopener noreferrer">
                                        <img src={githubIcon} alt="Github Icon" width="32px" height="32px"></img>
                                    </a>
                                </div>
                            </div>
                        </article>

                    )
                })}
            </div>
        </section >
    )

}

export default Projects